import * as React from "react";
import { cn } from "@/lib/utils";
import { Check } from "lucide-react";

export interface CheckboxProps
    extends Omit<React.InputHTMLAttributes<HTMLInputElement>, "type"> {
    label?: string;
    description?: string;
}

const Checkbox = React.forwardRef<HTMLInputElement, CheckboxProps>(
    ({ className, label, description, id, ...props }, ref) => {
        const checkboxId = id || React.useId();

        return (
            <div className="flex items-start gap-3">
                <div className="relative flex items-center justify-center">
                    <input
                        type="checkbox"
                        id={checkboxId}
                        ref={ref}
                        className="peer sr-only"
                        {...props}
                    />
                    <div
                        className={cn(
                            "h-5 w-5 shrink-0 rounded-md border-2 border-app-border bg-transparent transition-all duration-200",
                            "peer-checked:border-app-accent peer-checked:bg-app-accent",
                            "peer-focus-visible:ring-2 peer-focus-visible:ring-app-accent/20",
                            "peer-disabled:cursor-not-allowed peer-disabled:opacity-50",
                            className
                        )}
                    />
                    <Check
                        className="pointer-events-none absolute h-3 w-3 text-white opacity-0 transition-opacity duration-200 peer-checked:opacity-100"
                        strokeWidth={3}
                    />
                </div>
                {(label || description) && (
                    <div className="space-y-0.5">
                        {label && (
                            <label
                                htmlFor={checkboxId}
                                className="text-sm font-medium text-app-text cursor-pointer"
                            >
                                {label}
                            </label>
                        )}
                        {description && (
                            <p className="text-xs text-app-muted">{description}</p>
                        )}
                    </div>
                )}
            </div>
        );
    }
);
Checkbox.displayName = "Checkbox";

export { Checkbox };
