import * as React from "react";
import { cn } from "@/lib/utils";
import { cva, type VariantProps } from "class-variance-authority";

const badgeVariants = cva(
    "inline-flex items-center rounded-full px-3 py-1 text-xs font-semibold transition-colors",
    {
        variants: {
            variant: {
                default: "bg-app-card text-app-text border border-app-border",
                primary: "bg-app-accent/10 text-app-accent border border-app-accent/20",
                success: "bg-app-success/10 text-app-success border border-app-success/20",
                warning: "bg-app-warning/10 text-app-warning border border-app-warning/20",
                error: "bg-app-error/10 text-app-error border border-app-error/20",
                outline: "border-2 border-app-border bg-transparent text-app-text",
            },
        },
        defaultVariants: {
            variant: "default",
        },
    }
);

export interface BadgeProps
    extends React.HTMLAttributes<HTMLDivElement>,
    VariantProps<typeof badgeVariants> { }

const Badge = React.forwardRef<HTMLDivElement, BadgeProps>(
    ({ className, variant, ...props }, ref) => {
        return (
            <div
                ref={ref}
                className={cn(badgeVariants({ variant }), className)}
                {...props}
            />
        );
    }
);
Badge.displayName = "Badge";

export { Badge, badgeVariants };
