import * as React from "react";
import { cn } from "@/lib/utils";
import { ChevronDown } from "lucide-react";

export interface SelectProps
    extends React.SelectHTMLAttributes<HTMLSelectElement> {
    label?: string;
    error?: string;
    hint?: string;
    options: Array<{ value: string; label: string; disabled?: boolean }>;
    placeholder?: string;
}

const Select = React.forwardRef<HTMLSelectElement, SelectProps>(
    (
        { className, label, error, hint, options, placeholder, id, ...props },
        ref
    ) => {
        const selectId = id || React.useId();

        return (
            <div className="w-full space-y-2">
                {label && (
                    <label
                        htmlFor={selectId}
                        className="block text-sm font-medium text-app-text"
                    >
                        {label}
                        {props.required && <span className="ml-1 text-app-error">*</span>}
                    </label>
                )}
                <div className="relative">
                    <select
                        id={selectId}
                        ref={ref}
                        className={cn(
                            "flex h-12 w-full appearance-none rounded-xl border border-app-border bg-app-card px-4 py-3 pr-10 text-base text-app-text transition-all duration-200",
                            "focus:border-app-accent focus:outline-none focus:ring-2 focus:ring-app-accent/20",
                            "disabled:cursor-not-allowed disabled:opacity-50",
                            error &&
                            "border-app-error focus:border-app-error focus:ring-app-error/20",
                            className
                        )}
                        {...props}
                    >
                        {placeholder && (
                            <option value="" disabled>
                                {placeholder}
                            </option>
                        )}
                        {options.map((option) => (
                            <option
                                key={option.value}
                                value={option.value}
                                disabled={option.disabled}
                            >
                                {option.label}
                            </option>
                        ))}
                    </select>
                    <ChevronDown className="pointer-events-none absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 text-app-muted" />
                </div>
                {hint && !error && <p className="text-xs text-app-muted">{hint}</p>}
                {error && <p className="text-xs text-app-error">{error}</p>}
            </div>
        );
    }
);
Select.displayName = "Select";

export { Select };
