import * as React from "react";
import { cva, type VariantProps } from "class-variance-authority";
import { cn } from "@/lib/utils";

const buttonVariants = cva(
    "inline-flex items-center justify-center gap-2 whitespace-nowrap rounded-xl text-sm font-semibold transition-all duration-300 focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-app-accent focus-visible:ring-offset-2 focus-visible:ring-offset-app-bg disabled:pointer-events-none disabled:opacity-50 active:scale-[0.98]",
    {
        variants: {
            variant: {
                default:
                    "bg-white text-app-bg hover:bg-white/90 shadow-lg shadow-white/10",
                primary:
                    "bg-app-accent text-white hover:bg-app-accent/90 shadow-lg shadow-app-accent/25",
                secondary:
                    "bg-app-card text-app-text border border-app-border hover:bg-app-border/50",
                ghost: "hover:bg-app-card text-app-text",
                outline:
                    "border-2 border-app-border bg-transparent hover:bg-app-card text-app-text",
                destructive:
                    "bg-app-error text-white hover:bg-app-error/90 shadow-lg shadow-app-error/25",
                success:
                    "bg-app-success text-white hover:bg-app-success/90 shadow-lg shadow-app-success/25",
                link: "text-app-accent underline-offset-4 hover:underline",
            },
            size: {
                sm: "h-9 px-4 text-xs",
                default: "h-11 px-6 text-sm",
                lg: "h-13 px-8 text-base",
                xl: "h-14 px-10 text-lg",
                icon: "h-11 w-11",
            },
        },
        defaultVariants: {
            variant: "default",
            size: "default",
        },
    }
);

export interface ButtonProps
    extends React.ButtonHTMLAttributes<HTMLButtonElement>,
    VariantProps<typeof buttonVariants> {
    isLoading?: boolean;
}

const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(
    ({ className, variant, size, isLoading, children, disabled, ...props }, ref) => {
        return (
            <button
                className={cn(buttonVariants({ variant, size, className }))}
                ref={ref}
                disabled={disabled || isLoading}
                {...props}
            >
                {isLoading && (
                    <svg
                        className="h-4 w-4 animate-spin"
                        xmlns="http://www.w3.org/2000/svg"
                        fill="none"
                        viewBox="0 0 24 24"
                    >
                        <circle
                            className="opacity-25"
                            cx="12"
                            cy="12"
                            r="10"
                            stroke="currentColor"
                            strokeWidth="4"
                        />
                        <path
                            className="opacity-75"
                            fill="currentColor"
                            d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                        />
                    </svg>
                )}
                {children}
            </button>
        );
    }
);
Button.displayName = "Button";

export { Button, buttonVariants };
