import * as React from "react";
import { cn } from "@/lib/utils";

export interface InputProps
    extends React.InputHTMLAttributes<HTMLInputElement> {
    label?: string;
    error?: string;
    hint?: string;
    leftIcon?: React.ReactNode;
    rightIcon?: React.ReactNode;
}

const Input = React.forwardRef<HTMLInputElement, InputProps>(
    ({ className, type, label, error, hint, leftIcon, rightIcon, id, ...props }, ref) => {
        const inputId = id || React.useId();

        return (
            <div className="w-full space-y-2">
                {label && (
                    <label
                        htmlFor={inputId}
                        className="block text-sm font-medium text-app-text"
                    >
                        {label}
                        {props.required && <span className="ml-1 text-app-error">*</span>}
                    </label>
                )}
                <div className="relative">
                    {leftIcon && (
                        <div className="pointer-events-none absolute left-3 top-1/2 -translate-y-1/2 text-app-muted">
                            {leftIcon}
                        </div>
                    )}
                    <input
                        type={type}
                        id={inputId}
                        className={cn(
                            "flex h-12 w-full rounded-xl border border-app-border bg-app-card px-4 py-3 text-base text-app-text placeholder:text-app-muted transition-all duration-200",
                            "focus:border-app-accent focus:outline-none focus:ring-2 focus:ring-app-accent/20",
                            "disabled:cursor-not-allowed disabled:opacity-50",
                            error && "border-app-error focus:border-app-error focus:ring-app-error/20",
                            leftIcon && "pl-10",
                            rightIcon && "pr-10",
                            className
                        )}
                        ref={ref}
                        {...props}
                    />
                    {rightIcon && (
                        <div className="absolute right-3 top-1/2 -translate-y-1/2 text-app-muted">
                            {rightIcon}
                        </div>
                    )}
                </div>
                {hint && !error && (
                    <p className="text-xs text-app-muted">{hint}</p>
                )}
                {error && (
                    <p className="text-xs text-app-error">{error}</p>
                )}
            </div>
        );
    }
);
Input.displayName = "Input";

export { Input };
