import * as React from "react";
import { cn } from "@/lib/utils";

interface ProgressProps extends React.HTMLAttributes<HTMLDivElement> {
    value: number;
    max?: number;
    showLabel?: boolean;
    size?: "sm" | "md" | "lg";
}

const Progress = React.forwardRef<HTMLDivElement, ProgressProps>(
    ({ className, value, max = 100, showLabel, size = "md", ...props }, ref) => {
        const percentage = Math.min(Math.max((value / max) * 100, 0), 100);

        return (
            <div className="w-full space-y-2">
                {showLabel && (
                    <div className="flex justify-between text-sm">
                        <span className="text-app-muted">Progress</span>
                        <span className="font-medium text-app-text">{Math.round(percentage)}%</span>
                    </div>
                )}
                <div
                    ref={ref}
                    className={cn(
                        "w-full overflow-hidden rounded-full bg-app-border",
                        {
                            sm: "h-1.5",
                            md: "h-2.5",
                            lg: "h-4",
                        }[size],
                        className
                    )}
                    {...props}
                >
                    <div
                        className="h-full rounded-full bg-gradient-to-r from-app-accent to-blue-400 transition-all duration-500 ease-out"
                        style={{ width: `${percentage}%` }}
                    />
                </div>
            </div>
        );
    }
);
Progress.displayName = "Progress";

export { Progress };
